import Card from './Card';
import Direction from './Direction';
import Stack from './Stack';
import input from './input';

$('#modal').hide()

const config = {
  allowedDirections: [Direction.LEFT, Direction.RIGHT,]
}

let state = {
  'age': 0,
  'consequences': [],
  'score': 0,
  'outerIndex': 0,
  'innerIndex': 0,
  'maxInnerIndex': 0,
  'maxOuterIndex': 0,
}

const age = $('#age')
const keys = Object.keys(input)
keys.sort((a, b) => Number.parseInt(a) - Number.parseInt(b))

const createSlider = () => {
  var mySwiper = new Swiper('.swiper-container', {
    loop: true,
    speed: 400,
    spaceBetween: 5,
    slidesPerView: 3,
    slidesOffsetBefore: 10,
    slidesOffsetAfter: 10,
    grabCursor: true,
  })
}

const showResultsModal = () => {
  for(let i in state.consequences){
    $('#swiper-wrapper').append($('<div class="swiper-slide" > <p> ' + state.consequences[i] + ' </p> </div>'))
  }

  $('#modal').show()
  $("body").css("overflow", "hidden");
  createSlider()
}

let consumeCard = (target, direction) => {
  const key = keys[state.outerIndex]
  const kval = input[key][state.innerIndex]

  state.innerIndex += 1

  if(state.innerIndex == state.maxInnerIndex){
    state.outerIndex += 1
    state.innerIndex = 0
    
    if(state.outerIndex == state.maxOuterIndex){
      age.html('Game Over!')
      showResultsModal()
      return
    }
    else{
      age.html(keys[state.outerIndex] + ' years')
      state.maxInnerIndex = input[keys[state.outerIndex]].length
    }
  }

  let answer = direction == Direction.LEFT ? 0 : 1
  
  if(direction == Direction.LEFT){
    state.consequences.push(kval['Tip (Consequence on wrong answer)'])
  }
}

const initialize = (stack) => {
  const cardContainer = $('#viewport .stack')
  
  for (let i in keys) {
    let v = keys[keys.length - i - 1]

    for (let j in input[v]) {
      let k = input[v][j]
      let card = document.createElement('li')
      let cardText = document.createTextNode(k.Questions)
      card.appendChild(cardText)
      cardContainer.append(card)
      stack.createCard(card)
    }
  }

  state.maxInnerIndex = input[keys[0]].length
  state.maxOuterIndex = keys.length
}

document.addEventListener('DOMContentLoaded', function () {
  var stack;
  
  stack = Stack(config);
  
  age.html(keys[0] + ' years')

  initialize(stack)

  stack.on('throwout', function (e) {
    e.target.classList.remove('in-deck');
    e.target.classList.add('deleted');

    consumeCard(e.target, e.throwDirection)
  });

  stack.on('throwin', function (e) {
    e.target.classList.add('in-deck');
  });
});


